import Vue from 'vue'
import Vuex from 'vuex'
import axios from 'axios'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    query: '',
    videos: [],
    SelectedVideo: {},
    videosR: [],
    pageSearch: 10,
    pageRelated: 10,
  },
  getters: {
    isVideoSelected: state => !!Object.keys(state.SelectedVideo).length,
    isVideoSearched: state => !!state.videos.length,
  },
  mutations: {
    SEARCH_VIDEO: (state, videos) => { state.videos = videos },
    SELECT_VIDEO: (state, video) => { state.SelectedVideo = video },
    RESET: (state) => {
      state.SelectedVideo = {}
      state.videos= []
    },
    RELATED_VIDEO: (state, videos) => { state.videosR = videos },
    RESET_PAGE_S: state => state.pageSearch = 10,
    RESET_PAGE_R: state => state.pageRelated = 10,
    SAVE_QUERY: (state, query) => { state.query = query },
    INCREMENT: (state, page) => { page === 'pageSearch' ? state.pageSearch += 5 : state.pageRelated += 5 }
  },
  actions: {
    searchVideo({ commit }, query) {
      axios.get('https://www.googleapis.com/youtube/v3/search', {
        params: {
          key: process.env.VUE_APP_YOUTUBE_API_KEY,
          type: 'video',
          part: 'snippet',
          q: query,
        },
      })
        .then(res => commit('SEARCH_VIDEO', res.data.items))
        .catch(err => console.error(err))
      commit('SAVE_QUERY', query)
      commit('RESET_PAGE_S')
    },

    onVideoClick({ commit }, video) {
      commit('SELECT_VIDEO', video)
      // 화면 이동
      setTimeout(function() {
        const location = document.querySelector("#video-player").offsetTop;
        window.scrollTo({top:location, behavior:'smooth'});
      },0)
      // 관련 영상 찾기
      axios.get('https://www.googleapis.com/youtube/v3/search', {
        params: {
          key: process.env.VUE_APP_YOUTUBE_API_KEY,
          type: 'video',
          part: 'snippet',
          relatedToVideoId: video.id.videoId,
        },
      })
        .then(res => commit('RELATED_VIDEO', res.data.items))
        .catch(err => console.error(err))
        commit('RESET_PAGE_R')
    },

    Reset({ commit }) {
      commit('RESET')
    },

    onMoreVideo({ commit }) {
      axios.get('https://www.googleapis.com/youtube/v3/search', {
        params: {
          key: process.env.VUE_APP_YOUTUBE_API_KEY,
          type: 'video',
          part: 'snippet',
          q: this.state.query,
          maxResults: this.state.pageSearch
        },
      })
        .then(res => commit('SEARCH_VIDEO', res.data.items))
        .catch(err => console.error(err))
      
      commit('INCREMENT', 'pageSearch')
    },

    onMoreVideoR({ commit }) {
      axios.get('https://www.googleapis.com/youtube/v3/search', {
        params: {
          key: process.env.VUE_APP_YOUTUBE_API_KEY,
          type: 'video',
          part: 'snippet',
          relatedToVideoId: this.state.SelectedVideo.id.videoId,
          maxResults: this.state.pageRelated
        },
      })
        .then(res => commit('RELATED_VIDEO', res.data.items))
        .catch(err => console.error(err))
      
      commit('INCREMENT', 'pageRelated')
      console.log(this.state.pageRelated)
    },
  },
  modules: {
  }
})
