<template>
  <div class="container">
    <div class="row gx-2">
      <div class="col">
        <div class="ps-5 mb-3">Video List</div>
        <VideoListItem 
          v-for="video in videos"
          :key="video.etag"
          :video="video"
        />
        <li class="list-group-item video-item" @click="onMoreVideo" v-if="pageSearch <= 50">More</li>
      </div>
      <div class="col" v-if="isVideoSelected">
        <div class="mb-3">Related Video</div>
        <VideoListItem 
          v-for="video in videosR"
          :key="video.etag"
          :video="video"
        />
        <li class="list-group-item video-item" @click="onMoreVideoR" v-if="pageRelated <= 50">More</li>
      </div>
    </div>
  </div>
</template>

<script>
import VideoListItem from './VideoListItem.vue'
// import VideoListRelated from './VideoListRelated.vue'
import { mapActions } from 'vuex'

export default {
  name: 'VideoList',
  components: {
    VideoListItem,
    // VideoListRelated,
  },
  computed: {
    videos () {
      return this.$store.state.videos
    },
    videosR () {
      return this.$store.state.videosR
    },
    isVideoSelected() {
      return this.$store.getters.isVideoSelected
    },
    pageSearch() {
      return this.$store.state.pageSearch
    },
    pageRelated() {
      return this.$store.state.pageRelated
    }
  },
  methods: {
    ...mapActions(['onMoreVideo', 'onMoreVideoR'])
  }
}
</script>

<style>
.video-item {
  cursor: pointer;
}
</style>